// src/App.js
import React, { useState, useEffect } from "react";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
  useLocation,
} from "react-router-dom";
import ReactGA from "react-ga4";

// --- Component Imports ---
import Dashboard from "./components/Dashboard";
import PosterDisplay from "./components/PosterDisplay";
import LandingPage from "./components/LandingPage";
import DataErrorBoundary from "./components/DataErrorBoundary";

// --- Utils & Styles ---
import { loadAllData } from "./utils/dataLoader";
import "./styles/App.css";

// GA4 tracking component
function GAPageTracker() {
  const location = useLocation();

  useEffect(() => {
    ReactGA.send({ hitType: "pageview", page: location.pathname });
  }, [location]);

  return null;
}

function App() {
  // --- State and useEffect for data loading remain the same ---
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [loadingStage, setLoadingStage] = useState("Initializing...");

  useEffect(() => {
    // Initialize Google Analytics - replace with your measurement ID
    ReactGA.initialize("G-LRR8N75MLG");

    const fetchData = async () => {
      try {
        setLoadingStage("Preparing to load data...");
        setLoadingStage("Loading data...");
        const loadedData = await loadAllData();
        setData(loadedData);
        setLoading(false);
      } catch (err) {
        console.error("Error loading data in App.js:", err);
        setError(`${err.message}`);
        setLoading(false);
      }
    };
    fetchData();
  }, []);

  // --- Loading and Error states remain the same ---
  if (loading) {
    return (
      <div className="loading-container">
        <div className="loading-stage">{loadingStage}</div>
        <div className="loading-message">This should only take a moment.</div>
      </div>
    );
  }
  if (error) {
    return (
      <div className="error-container">
        <div className="error-title">Error Loading Data</div>
        <div className="error">{error}</div>
        <div className="troubleshooting">
          <h3>Troubleshooting Steps:</h3>
          <ol>
            <li>Verify that your CSV files exist in the public/data folder</li>
            <li>
              Check that your CSV files have the correct structure and headers
            </li>
            <li>Try refreshing the page</li>
            <li>Restart your development server (npm start)</li>
            <li>
              Check your browser's console (F12) for more detailed error
              messages
            </li>
          </ol>
          <button
            onClick={() => window.location.reload()}
            className="reload-button"
          >
            Reload Page
          </button>
        </div>
      </div>
    );
  }

  // --- Success State ---
  return (
    <Router basename={process.env.PUBLIC_URL}>
      <div className="App">
        {/* Add GA Page Tracker */}
        <GAPageTracker />

        <Routes>
          <Route path="/" element={<LandingPage />} />
          <Route
            path="/dashboard"
            element={
              <DataErrorBoundary>
                <Dashboard data={data} />
              </DataErrorBoundary>
            }
          />
          <Route path="/poster" element={<PosterDisplay />} />
          <Route path="*" element={<Navigate to="/" replace />} />
        </Routes>
      </div>
    </Router>
  );
}

export default App;
